*,
*::after,
*::before {
    margin: 0px;
    padding: 0px;
    box-sizing: inherit;
}

.margin-top-small {
    margin-top: 20px;
}

body {
    text-align: center;
    box-sizing: border-box;
    color: #2d3436;
    background-color: #ecf0f1;
    font-family: 'Raleway', sans-serif;
}

button {
  background-color: #ED4C67;
  padding: 10px;
  border-radius: 100px;
  border: none;
  color: #eee;
  cursor: pointer;
  margin: 5px;
}

button:focus {
    outline: none;
}

.primary-header {
    font-family: 'Aclonica', sans-serif;
}

.scale {
    color: #ecf0f1;
    background-color: #1289A7;
    border: 1px solid #1289A7;
    border-radius: 50%;
}

.root {
    color: #ecf0f1;
    background-color: #12CBC4;
    border: 1px solid #ED4C67;
    border-radius: 50%;
}

.dashboard {
    border: 3px solid black;
    width: 40%;
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.dashboard__header {
    grid-column: 1 / -1;
}
